<template>
  <div id="echartsId" :ref="chartRef" style="width:100%; height:100%; min-height:200px"></div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props: ["option"],
  methods: {
    drawChart() {
      let chart = this.$echarts.init(this.$refs[this.chartRef]);
      chart.setOption(this.option);
    }
  },
  mounted() {
    this.drawChart();
  },
  computed: {
    chartRef() {
      let time = new Date();
      return "chart" + this.$md5(time.toLocaleDateString());
    }
  }
};
</script>

<style></style>
